/* картинки в качестве фона слайдера */
.inner-test {
  text-align: center;
  background-color: #fff;
  border-left: 3px solid #DC143C; }

.inner-test img {
  max-width: 160px;
  max-height: 160px;
  float: left;
  border-radius: 10em;
  margin: 10px 5px 30px 20px; }

.inner-test-cont {
  margin-left: 220px; }

p.text-testimonial {
  text-align: left;
  color: #626262;
  line-height: 2; }

.text-testimonial i {
  margin-right: 5px;
  color: #DC143C; }

/*стили кнопки наверх*/
.button-top {
  position: fixed;
  bottom: 20px;
  right: -120px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  background-color: #DC143C;
  cursor: pointer;
  text-align: center;
  opacity: .3;
  z-index: 3;
  transition: all 0.5s ease; }

.button-top .fa {
  color: #fff;
  font-size: 1.2rem; }

.button-top:hover {
  opacity: 1; }

.button-top.active {
  right: 30px; }

footer {
  padding: 20px 0;
  background-color: #333;
  color: #eee; }

footer p {
  text-align: center;
  font-size: 15px;
  line-height: 35px;
  letter-spacing: 1px; }

/* стили формы обратной связи */
.forms-call {
  font-size: .875em; }

.forma-header p {
  font-size: 0.938em;
  color: #666;
  margin-top: 22px;
  text-align: left; }

.forms-call label {
  margin: 10px 0;
  display: block; }

.forms-call input,
.forms-call textarea {
  display: block;
  width: 100%;
  height: 60px;
  line-height: 60px;
  font-size: 0.938em;
  border: none;
  border-bottom: 1px solid red;
  text-indent: 10px;
  margin: 10px auto;
  -moz-transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  color: #364052; }

.second_label {
  padding-left: 10%; }

.first_label {
  padding-right: 10%; }

.forms-call textarea {
  height: 150px;
  width: 100%;
  resize: none;
  overflow: hidden; }

.forms-call input:focus,
.forms-call textarea:focus {
  color: #222833; }

.forms-calldecor input {
  height: 40px;
  line-height: 40px; }

.forms-calldecor textarea {
  height: 100px; }

.forms-calldecor .button-call .get-buttons {
  margin-top: 10px;
  line-height: 1; }

.forms-call .get-buttons {
  margin-top: 50px;
  padding: 10px 50px;
  /* 	line-height: 50px; */
  font-size: 18px;
  border: 1px solid #DC143C;
  text-decoration: none;
  background-color: #DC143C;
  border-radius: 3px;
  -moz-transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  color: #fff; }

.button-call {
  text-align: center;
  margin: 0;
  float: left; }

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active {
  background-color: transparent;
  border: 1px solid #DC143C;
  color: #DC143C; }

/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
  max-width: 480px;
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.5s else;
  overflow: hidden;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 1em; }

.forms-calldecor h4,
.forms-calldecor h5 {
  color: #fff;
  text-align: center;
  font-size: 1.3em;
  margin: 50px 20px 0; }

.forms-calldecor h5 {
  margin-bottom: 30px;
  margin-top: 10px;
  font-size: 0.96em;
  letter-spacing: 1px; }

.forms-calldecor input,
.forms-calldecor textarea {
  width: 90%;
  color: white; }

.forms-calldecor .button-call {
  float: none;
  text-align: center;
  color: #fff;
  margin-bottom: 40px; }

.forms-calldecor .mfp-close {
  position: absolute;
  right: 15px;
  top: 15px;
  border-radius: 3px; }

.forms-calldecor .mfp-close-btn-in .mfp-close {
  color: #fff;
  font-size: 1.5em;
  font-weight: bold;
  border-radius: 3px; }

.mfp-close-btn-in .mfp-close {
  color: #ffffff; }

.forms-calldecor .success {
  background-color: #000;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  opacity: 0;
  padding-right: 20px;
  padding-left: 20px;
  transition: all 0.5s ease; }

.forms-calldecor .success.active {
  top: 0;
  opacity: 1; }

.forms-calldecor .success p {
  text-transform: uppercase;
  display: flex;
  color: #fff;
  font-size: 20px;
  margin: auto;
  text-align: center; }

/* overlay at start */
.mfp-fade.mfp-bg {
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8; }

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0; }

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0;
  left: 100%;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  left: 0; }

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
  left: 100%; }

