/* all styles  */

.block {
	padding: 120px 0;
}

.block-fongrey {
	background-color: $fon-gray;
}

/* блок about me  */
.block-man {
	margin: 0 auto;
	width: 287px;
	min-height: 371px;
	// background-image: url("../images/man.png");
	background: url('../images/man.webp') top center no-repeat;
	background-size: cover;
}

.about-me-block {
	margin: 20px 0;
}

.about-me-title {
	font-family: "OpenSansSemiBold", sans-serif;
	font-size: 30px;
	line-height: 33px;
	text-transform: uppercase;
	position: relative;
	font-weight: normal;


}

.about-me-title:before {
	content: "";
	position: absolute;
	bottom: -10px;
	width: 5em;
	border: 1px solid $red-color;

}

.about-me-text {
	margin-top: 25px;
	font-size: 16px;
	line-height: 30px;
	letter-spacing: 1px;

}

.block .about-me-title {
	text-align: center;
	margin-bottom: 70px;
}

.block .about-me-title:before {
	left: 50%;
	margin-left: -3em;
	width: 6em;
}

/* services block style  */
.service-block {
	text-align: center;
}

.service-block-icon {
	color: #dc143c;
}

.service-block-title {
	font-family: "OpenSansSemiBold", sans-serif;
	font-size: 18px;
	line-height: 19px;
	text-transform: uppercase;
	margin: 30px 0 20px;
}

.service-block-text {
	font-size: 14px;
	line-height: 30px;
	letter-spacing: 1px;
	padding-left: 10px;
	padding-right: 10px;
}

/* block portfolio */
.block-portfolio .col-md-4 {
	padding: 0;

}

.effect-boom {
	position: relative;
}

.effect-boom img {
	width: 100%;
	height: auto;
}

.effect-boom-blok {

	overflow: hidden;
}

.effect-boom-blok .caption {
	position: absolute;
	top: 0px;
	left: 0px;
	background: rgba(0, 5, 40, 0.8);
	width: 100%;
	height: 100%;
	color: #fff;
	opacity: 0;
	transform: rotate(30deg) translate(30px, -89px);
	transition: transform 0.55s cubic-bezier(0.49, -0.19, 0.7, -0.01) 0.1s, opacity 0.1s linear 0s;
}

.effect-boom-blok .caption h4 {
	font-size: 21px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;

	padding-bottom: 20px;
	margin-top: 80px;
}

.effect-boom-blok .caption p {
	margin: 15px 0px 35px;
	text-align: center;
	font-style: italic;
	padding: 0px 10px;
}

.effect-boom-blok .caption ul {
	border: 1px solid #fff;
	padding: 4px 0;
	text-align: center;
}

.effect-boom-blok .caption ul li {
	display: inline-block;
	list-style-type: none;
}

.effect-boom-blok .caption ul li a.btn {
	display: inline-block;
	text-align: center;
	background: rgb(0, 0, 0);
	border: 2px solid rgba(darken(#eee, 80%), 0.7);
	color: #fff;
	width: 60px;
	font-size: 14px;
	height: 60px;
	line-height: 60px;
	border-radius: 50%;
	margin: 0px auto 0px auto;
}

.effect-boom-blok img {
	display: block;
	width: 100%;
	height: auto;
	transition: all 0.25s linear 0.3s;
	transform: scale(1);
}

.effect-boom-blok:hover img {
	transform: scale(1.3);
	transition-delay: 0s;
}

.effect-boom-blok:hover .caption {
	transform: rotate(0deg) translate(0px, 0px);
	opacity: 1;
	transition-delay: 0.3s;
}

/*  оформление слайдера отзывов */

#carrousel .owl-theme .owl-nav {
	position: relative;
}

#carrousel .owl-nav .owl-prev,
#carrousel .owl-nav .owl-next {
	position: absolute;
	top: -19px;
	background-color: transparent;
	color: transparent;
}

#carrousel .owl-nav .owl-prev {
	left: 41%;
}

#carrousel .owl-nav .owl-next {
	right: 42%;
}

#carrousel .owl-nav .owl-prev:before {
	position: absolute;
	content: "\f104";
	font-family: "FontAwesome";
	top: 0;
	left: 0;
	font-weight: normal;
	font-size: 3.0rem;
	line-height: 1;
	color: #aeaeae;
}

#carrousel .owl-nav .owl-next:before {
	position: absolute;
	content: "\f105";
	font-family: "FontAwesome";
	top: 0;
	left: 0;
	font-weight: normal;
	font-size: 3.0rem;
	line-height: 1;
	color: #aeaeae;

}

#carrousel .owl-nav .owl-prev:hover:before,
#carrousel .owl-nav .owl-next:hover:before {
	color: darken($red-color, 10%);
}

/* Отзывы  */
.inner-test {
	text-align: center;
	background-color: #fff;
}

.inner-test img {
	max-width: 128px;
	margin: 0 auto 15px;
}

h3.city-name {
	padding: 10px 0;
	font-size: 14px;
	font-size: .8em;
	font-weight: 900;
	line-height: 20px;
	color: $red-color;
	text-align: left;
	margin-left: 30px;
}

p.text-testimonial {
	padding: 10px 0 5px;
	font-size: 16px;
	letter-spacing: 1px;
	line-height: 35px;
}

.block-items {
	cursor: default;
}

/*Стрелки*/
.owl-my-nav {
	position: absolute;
	width: 100%;
	height: 100%;
}

/*Изменяем внешний вид элементов управления (точки) */
.owl-controls {
	margin-top: 25px;
}

.owl-theme .owl-dots .owl-dot span {
	width: 12px;
	height: 12px;
	background-color: darken(#eee, 25%);
}

.owl-theme .owl-dots .owl-dot.active span {
	width: 12px;
	height: 12px;
}

.owl-carousel .owl-dots .owl-dot.active span {
	background-color: darken($red-color, 10%);
}

.owl-carousel .owl-dots .owl-dot:hover span {
	background-color: darken($red-color, 20%);
}

/* картинки в качестве фона слайдера */
.inner-test {
	text-align: center;
	background-color: #fff;
	border-left: 3px solid $red-color;
}

.inner-test img {
	max-width: 160px;
	max-height: 160px;
	float: left;
	border-radius: 10em;
	margin: 10px 5px 30px 20px;

}

.inner-test-cont {
	margin-left: 220px;

}

p.text-testimonial {
	text-align: left;
	color: darken(#eee, 55%);

	line-height: 2;

}

.text-testimonial i {
	margin-right: 5px;
	color: $red-color;
}

/*стили кнопки наверх*/
.button-top {
	position: fixed;
	bottom: 20px;
	right: -120px;
	width: 30px;
	height: 30px;
	line-height: 30px;
	border-radius: 50%;
	background-color: $red-color;
	cursor: pointer;
	text-align: center;
	opacity: .3;
	z-index: 3;
	transition: all 0.5s ease;
}

.button-top .fa {
	color: #fff;
	font-size: 1.2rem;
}

.button-top:hover {
	opacity: 1;
}

.button-top.active {
	right: 30px;
}

footer {
	padding: 20px 0;
	background-color: #333;
	color: #eee;
}

footer p {
	text-align: center;
	font-size: 15px;
	line-height: 35px;
	letter-spacing: 1px;
}

/* стили формы обратной связи */

.forms-call {
	font-size: .875em;
}

.forma-header p {
	font-size: 0.938em;
	color: #666;
	margin-top: 22px;
	text-align: left;
}

.forms-call label {
	margin: 10px 0;
	display: block;
}

.forms-call input,
.forms-call textarea {
	display: block;
	width: 100%;
	height: 60px;
	line-height: 60px;
	font-size: 0.938em;
	border: none;
	border-bottom: 1px solid red;
	text-indent: 10px;
	margin: 10px auto;
	@include trans;
	color: #364052;
}

.second_label {
	padding-left: 10%;
}

.first_label {
	padding-right: 10%;
}

.forms-call textarea {
	height: 150px;
	width: 100%;
	resize: none;
	overflow: hidden;
}

.forms-call input:focus,
.forms-call textarea:focus {
	color: darken(#364052, 10%);
}

.forms-calldecor input {
	height: 40px;
	line-height: 40px;
}

.forms-calldecor textarea {
	height: 100px;
}

.forms-calldecor .button-call .get-buttons {
	margin-top: 10px;
	line-height: 1;
}

.forms-call .get-buttons {
	margin-top: 50px;
	padding: 10px 50px;
	/* 	line-height: 50px; */
	font-size: 18px;
	border: 1px solid $red-color;
	text-decoration: none;
	background-color: $red-color;
	border-radius: 3px;
	@include trans;
	color: #fff;
}

.button-call {
	text-align: center;
	margin: 0;
	float: left;
}

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active {
	background-color: transparent;
	border: 1px solid $red-color;
	color: $red-color;
}

/* конец формы обратной связи */

/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
	max-width: 480px;
	position: relative;
	background-color: rgba(0, 0, 0, 0.5);
	transition: all 0.5s else;
	overflow: hidden;
	margin: auto;
	border: 1px solid #fff;
	border-radius: 1em;
}

.forms-calldecor h4,
.forms-calldecor h5 {
	color: #fff;
	text-align: center;
	font-size: 1.3em;
	margin: 50px 20px 0;
}

.forms-calldecor h5 {
	margin-bottom: 30px;
	margin-top: 10px;
	font-size: 0.96em;
	letter-spacing: 1px;
}

.forms-calldecor input,
.forms-calldecor textarea {
	width: 90%;
	color: lighten(#fff, 10%);
}

.forms-calldecor .button-call {
	float: none;
	text-align: center;
	color: #fff;
	margin-bottom: 40px;
}

.forms-calldecor .mfp-close {
	position: absolute;
	right: 15px;
	top: 15px;
	border-radius: 3px;
}

.forms-calldecor .mfp-close-btn-in .mfp-close {
	color: #fff;
	font-size: 1.5em;
	font-weight: bold;
	border-radius: 3px;
}

.mfp-close-btn-in .mfp-close {
	color: #ffffff;
}

.forms-calldecor .success {
	background-color: #000;
	position: absolute;
	z-index: 1;
	left: 0;
	top: -100%;
	width: 100%;
	height: 100%;
	display: flex;
	opacity: 0;
	padding-right: 20px;
	padding-left: 20px;
	transition: all 0.5s ease;
}

.forms-calldecor .success.active {
	top: 0;
	opacity: 1;
}

.forms-calldecor .success p {
	text-transform: uppercase;
	display: flex;
	color: #fff;
	font-size: 20px;
	margin: auto;
	text-align: center;
}

/* overlay at start */
.mfp-fade.mfp-bg {
	opacity: 0;

	-webkit-transition: all 0.5s ease;
	-moz-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
	opacity: 0.8;
}

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
	opacity: 0;
}

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
	opacity: 0;
	left: 100%;
	-webkit-transition: all 0.5s ease;
	-moz-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
	opacity: 1;
	left: 0;
}

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
	opacity: 0;
	left: 100%;
}
